import React from "react";
import Menu from '../Menu';
import Header from "../Header";
import MainBackground from "../MainBackground";
import IntakeForm from "../IntakeForm";
import { useEffect, useState } from 'react';
import "../css/FAQPageStyles.css";

function oneQuestionAndAnsewer(question, index, expanded, setExpanded){
  return(
    <div>
      <div 
        className="faq-page-question" 
        style={question.label === 'DISCLAIMER' ? {fontSize: '20px', fontStyle: 'italic'} : {}}
        onClick={() => setExpanded(expanded === index ? -1 : index)}
      >
        {question.label}
      </div>
      {expanded === index && (
        <div 
          className="faq-page-answer" 
          style={question.label === 'DISCLAIMER' ? {fontSize: '20px', fontStyle: 'italic'} : {}}
        >
          {question.answer}
        </div>
      )}
      <hr/>
    </div>
  )
}


function FAQPage(props){
  // State variable for the expandable list
  const [expanded, setExpanded] = useState(-1);

  document.title = "Priority Scan";
  const faqs = [
    {
      label: 'DISCLAIMER',
      answer: `This website is independently operated and has no affiliation with any hospitals or imaging departments. It is designed to assist users in identifying potential locations that may have shorter wait times for medical imaging services. Please note that all appointment times displayed are estimates and are based on the median wait times for CT and MRI at each location, as per data from Ontario Health. These times are subject to change and should be used as a guideline only. This website is not responsible for submitting or managing requests for appointments. It does not facilitate the completion or submission of any forms required by healthcare facilities. Users seeking to book an imaging appointment at any specified center must do so through their doctor's office. We strongly recommend verifying the current wait times and appointment details with the respective facility.`
    },
    {
      label: 'What is PriorityScan?',
      answer: `PriorityScan is an online platform that utilizes information collected by Ontario Health to help you find the shortest waiting times for CT and MRI scans in your area.`
    },
    {
      label: 'How does PriorityScan work?',
      answer: 'Simply input your postal code, the type of scan you need (CT or MRI), and how far you are willing to travel. PriorityScan will then provide you with a list of healthcare facilities with the shortest wait times within your specified range.'
    },
    {
      label: 'What information is required to use PriorityScan?',
      answer: 'You need to provide your postal code, the type of scan you need (CT or MRI), and your travel radius in kilometers.'
    },
    {
      label: 'How is my data used?',
      answer: 'Your data, including demographic information, location, and existing wait times, is used to help predict and provide you with the most accurate information on CT and MRI scan wait times in your area. This data is also used to improve our service and better understand trends in healthcare wait times. Please note that we take your privacy very seriously and anonymize all data to protect your personal information.'
    },
    {
      label: 'Is there a cost associated with using PriorityScan?',
      answer: 'As of now, PriorityScan is in its beta phase, and we are pleased to offer our services free of charge during this period. Our goal is to refine and optimize the platform to best meet the needs of our users. Please be advised that in the future, upon conclusion of the beta phase, there will be a fee associated with accessing the data provided by PriorityScan. We are committed to transparency and will communicate any changes to our pricing structure in a timely manner.'
    },
    {
      label: 'How accurate is the waiting time information provided by PriorityScan?',
      answer: 'PriorityScan utilizes the latest data from Ontario Health to provide accurate and up-to-date waiting times for CT and MRI scans. Our database is typically updated on a monthly basis, aligning with the updates from Ontario Health. Furthermore, our predictions are backed by a robust dataset comprising 15 years of historical data, which enhances the reliability of the information we provide. Rest assured, we are committed to delivering precise and timely data to assist you in making informed decisions about your healthcare needs.'
    },
    {
      label: 'Does PriorityScan cover all areas in Ontario?',
      answer: 'PriorityScan provides coverage for all publicly accessible MRI and CT scanners in Ontario, using data from healthcare facilities that report to Ontario Health. Please note our database is contingent upon the availability of data from each facility. If a facility does not contribute data to Ontario Health, it will not be included in our listings.'
    },
    {
      label: 'Can PriorityScan help me schedule my scan?',
      answer: 'PriorityScan provides information on waiting times to help you make an informed decision, but we do not schedule the scans ourselves. You will need to contact the healthcare facility directly to schedule your scan.'
    },
  ]
  return (
    <div className="App">
      <Header></Header>
      <Menu isLoged={props.isLoged} weight="faq"></Menu>
      <div id="faq-page-main-container">
        <div className="headerText">FAQ</div>
        {faqs.map((faq, index) => oneQuestionAndAnsewer(faq, index, expanded, setExpanded))}
      </div>
      {/* ... */}
    </div>
  );
}

export default FAQPage;