import React from "react";
import Menu from '../Menu';
import Header from "../Header";
import MainBackground from "../MainBackground";
import IntakeForm from "../IntakeForm";
import { useEffect, useState } from 'react';
import NotLoginBlock from "../NotLoginBlock";
import '../css/SearchPageStyles.css'

function SearchPage(props){
  document.title = "Priority Scan";
  if (props.isLoged) return SearchPageAccessed(props.isLoged)
  else return (<NotLoginBlock weight='search'></NotLoginBlock>)
}

function SearchPageAccessed(isLoged){
  return (
    <div className="App">
      <Header></Header>
      <Menu isLoged={isLoged} weight="search"></Menu>
      <div id="seacrh-page-data-container">
        <div className="headerText">This is your search page</div>
        <div className="descriptionText">Coming soon: You will soon be able to search for information.</div>
      </div>

    </div>
  );
}

export default SearchPage;