import React from "react";
import { useEffect, useState } from 'react';
import '../css/BookPageStyles.css';
import Button from "../Button";
import Header from "../Header";
import Menu from "../Menu";
import "../css/SignUpPageStyles.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const {LINK} = require('../../config.js')



async function sendSignUpRequest(){
    var data = {
        email: document.getElementById('email').value,
        password: document.getElementById('password').value
    }
    try {
        if (!data.password || !document.getElementById('password_repeat') || !data.email){
            toast.warn('All fields are mandatory', {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                return;
        }
        console.log(`password: ${data.password !== document.getElementById('password_repeat').value}`)
        if (data.password !== document.getElementById('password_repeat').value){
            toast.warn('Password must be the same as repeated password.', {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            // alert('Password must be the same as repeated password.')
            return;
        }
        console.log(`Sent request ${data.email}`)
        console.log(LINK)
        const proxyUrl = LINK || 'http://16.16.201.50:81';
        const res = await fetch('/api/users/register', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        });
        const answer = await res.text();

        if (res.status === 201) {
            toast.success('Registered user successfully! Now you can login in your profile!', {
                position: "top-center",
                autoClose: false,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            // alert('Registered user successfully! Now login in your new profile!')
        } else {
            toast.warn(answer, {
                position: "top-center",
                autoClose: false,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            // alert(answer);
            // TODO: CHANGE ALERT TO MORE BEAUTIFULL STYLE
            // console.log
        }
    } catch (error) {
        console.error('There was an error:', error);
    }
}

function SignUpPage() {
    return (
        <div>
            <Header></Header>
            <Menu weight='login'></Menu>
            <div id="sign-up-main-container">
                <div id="first-string-sign-up-page">Welcome to our website!</div>
                <div id="second-string-sign-up-page">Join and create your account.</div>
                <input className="form-control input-sign-up" type="email" placeholder="Email" id="email" />
                <input className="form-control input-sign-up" type="password" placeholder="Password" id="password" />
                <input className="form-control input-sign-up" type="password" placeholder="Repeat password" id="password_repeat" />
                <Button id="sign-up-sign-up-button" onClick={sendSignUpRequest} name="Sign Up" />
                <div id="bottom-text-sign-up-page">Or <a href='/login'><i class="fas fa-link">Login</i></a></div>
            </div>
        </div>
        // TODO: CHECK IS PASSWORD REPEATED RIGHT
    )
}

export {SignUpPage, sendSignUpRequest};
