import React from "react";
import Button from "./Button";
import { useState } from "react";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const {LINK} = require('../config.js')


function ProfileDataField(fieldName, data){
    return (
    <div className="profile-page-data-field-container">
      <div className="profile-page-data-field-name">{fieldName}</div>
      <div className="profile-page-data-field-value">{data ? data : "No data"}</div>
    </div>)
  }
  
function ProfileDataInput(fieldName, data, varName, useStateFunction){
    const func = useStateFunction
    
    localStorage.getItem(varName) === 'undefined' ? localStorage.setItem(varName, '') : console.log('')
    return (
    <div className="profile-page-data-field-container">
        <div className="profile-page-data-field-name">{fieldName}</div>
        <input type="text" value={data || localStorage.getItem(varName)} onChange={(event) => {func(event.target.value); console.log(data)}} />
    </div>)
  }


function SaveDataToServer(firstName, lastName, birthday, address, phoneNumber, familyDoctorName, email, password, setNewData){
    const id = toast.loading("Please wait...")
    console.log(`firstName ${firstName}`)
    var isDataGot;
    const proxyUrl = LINK || 'http://16.16.201.50:81';
    console.log(`LINK: ${proxyUrl}`)
    const data = {
        firstName: firstName,
        lastName: lastName,
        birthday: birthday,
        address: address,
        phoneNumber: phoneNumber,
        familyDoctorName: familyDoctorName,
        email: email,
        password: password,
        accessToken: localStorage.getItem('accessToken'),
        oldEmail: localStorage.getItem('userEmail'),
        oldPassword: localStorage.getItem('userPassword')
    }
    data.password ? data.password = data.password : data.password = localStorage.getItem('userPassword')
    data.email ? data.email = data.email : data.email = localStorage.getItem('userEmail')
    console.log(data)
    const response = fetch(
        '/api/users/updateuserdata', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    })
    .then(response => {
        return response.json();
    })
    .then(data => {
        toast.dismiss(id);
        if (data['message']){
            toast(data['message'], {
                position: "top-center",
                autoClose: false,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            // toast.update(id, { render: , type: toast.TYPE.WARNING, isLoading: false });
            isDataGot = false;
            return;
        }
        else{
            isDataGot = true
            console.log('setNewData')
            setNewData(data['newEmail'], data['newPassword'])
        }
    });
    return isDataGot
}

function ChangeProfileData(props){
    console.log(`user:`)
    setTimeout(500)
    const user = props.user
    console.log(user)
    
    localStorage.setItem('firstName', user['firstName'])
    localStorage.setItem('lastName', user['lastName'])
    localStorage.setItem('birthday', user['birthday'])
    localStorage.setItem('address', user['address'])
    localStorage.setItem('phoneNumber', user['phoneNumber'])
    localStorage.setItem('familyDoctorName', user['familyDoctorName'])
    localStorage.setItem('email', user['email'])
    localStorage.setItem('password', user['password'])

    const [change, setChange] = useState(false)
    const [firstName, firstNameChange] = useState(user.firstName)
    const [lastName, lastNameChange] = useState(user.lastName)
    const [birthday, birthdayChange] = useState(user.birthday)
    const [address, addressChange] = useState(user.address)
    const [phoneNumber, phoneNumberChange] = useState(user.phoneNumber)
    const [familyDoctorName, familyDoctorNameChange] = useState(user.familyDoctorName)
    const [email, emailChange] = useState(user.email)
    const [password, passwordChange] = useState(user.password)    
    console.log(`
    ${change}
    ${firstName}
    ${lastName}
    ${birthday}
    ${address}
    ${phoneNumber}
    ${familyDoctorName}
    ${email}
    ${password}
    `)
    const setNewData = (email, password) => {
        console.log(`setNewData ${email} ${password}`)
        localStorage.setItem('userEmail', email)
        localStorage.setItem('userPassword', password)
    }
    return(
        <div id="user-data-container">
            {change === true ? (
                <div>
                <div>First name</div>
                {ProfileDataInput('First name', firstName, 'firstName', firstNameChange)}
                <div>Last name</div>
                {ProfileDataInput('Last name', lastName, 'lastName', lastNameChange)}
                <div>Birthday</div>
                {ProfileDataInput('Birthday', birthday, 'birthday', birthdayChange)}
                <div>Address</div>
                {ProfileDataInput('Address', address, 'address', addressChange)}
                <div>Phone number</div>
                {ProfileDataInput('Phone number', phoneNumber, 'phoneNumber', phoneNumberChange)}
                <div>Family doctor name</div>
                {ProfileDataInput('Family doctor name', familyDoctorName, 'familyDoctorName', familyDoctorNameChange)}
                <div>Email</div>
                {ProfileDataInput('Email', email, 'email', emailChange)}
                <div>Password</div>
                {ProfileDataInput('Password', password, 'password', passwordChange)}
                <Button name="Save" onClick={async () => {
                    await SaveDataToServer(firstName, lastName, birthday, address, phoneNumber, familyDoctorName, email, password, setNewData)
                        setChange(false)
                        toast.success('Data saved! Reload page.', {
                            position: "top-center",
                            autoClose: false,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                            theme: "light",
                            });
                        setTimeout(1000)
                        // window.location.assign('/profile')                    
                    }} />
                <Button name="Cancel" onClick={() => {setChange(false)}} />
                </div>
            ) : (
                <div>
                {ProfileDataField('First name', props.user.firstName)}
                {ProfileDataField('Last name', props.user.lastName)}
                {ProfileDataField('Birthday', props.user.birthday)}
                {ProfileDataField('Address', props.user.address)}
                {ProfileDataField('Phone number', props.user.phoneNumber)}
                {ProfileDataField('Family doctor name', props.user.familyDoctorName)}
                {ProfileDataField('Email', props.user.email)}
                {ProfileDataField('Password', props.user.password)}
                <Button name="Change values" onClick={() => {setChange(true)}} />    
                </div>
            )}
        </div>
    )
}

export default ChangeProfileData;