import React, { useState, useEffect } from "react";
import {loadStripe} from '@stripe/stripe-js';
import { Elements } from "@stripe/react-stripe-js";
import Menu from '../Menu';
import Header from "../Header";
import MainBackground from "../MainBackground";
import IntakeForm from "../IntakeForm";
import Button from "../Button";
import '../css/BookPageStyles.css';
import NotLoginBlock from "../NotLoginBlock";
import isLoged from "../functions/IsLoged";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import CheckoutForm from "../checkoutform";


// Public Stripe API key
const stripePromise = loadStripe('pk_test_51OBOTjCzamtkVis6hhJbG7SaFnPwUnZJFKd8QCptNP3AIT3OoKy0voBKFTvWaNdPrwOxsBTqpFddy52pHsXEaZMX00KCtimL23');
// The CheckoutForm component where you collect and handle card details

function PaymentForm() {
  const [clientSecret, setClientSecret] = useState("");

  useEffect(() => {
    // Create PaymentIntent as soon as the page loads
    fetch("/api/create-payment-intent", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ items: [{ id: "wait-time-data" }] }),
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, []);

  const appearance = {
    theme: 'stripe',
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
    <div className="App">
      {clientSecret && (
        <Elements options={options} stripe={stripePromise}>
          <CheckoutForm />
        </Elements>
      )}
    </div>
  );
}

function BookPage(props){
  document.title = "Priority Scan";
  if (props.isLoged) return BookPageAccessed(isLoged)
  else return (<NotLoginBlock weight='book'></NotLoginBlock>)
}

function showInfoMessage(){
  toast.info('Coming soon: You will soon be able to download the form for your desired location', {
    position: "top-center",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "light",
    });
}


function BookPageAccessed(isLoged){ 
  // State variable for whether the user has paid
  const [hasPaid, setHasPaid] = useState(false); // Set this to true when the user pays

  const [clientSecret, setClientSecret] = useState('');

  useEffect(() => {
    // Create a Checkout Session as soon as the page loads
    fetch("/api/create-checkout-session", {
      method: "POST",
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, []);

  // State variable to control the display of the payment form
  const [showPaymentForm, setShowPaymentForm] = useState(false);

    // Function to handle the pay button click
    const handlePayClick = () => {
      setShowPaymentForm(true); // Show the payment form
    };

  var USER_CURRENT_APPT = JSON.parse(localStorage.getItem('USER_CURRENT_APPT'))
  var USER_NEW_APPT = JSON.parse(localStorage.getItem('NEWLY_FOUND_APPT'))
  
  if (USER_NEW_APPT){
    const targetDate = USER_NEW_APPT.appointmentDate;
    const targetDateObject = new Date(targetDate);
    const currentDate = new Date();
    const timeDifference = targetDateObject - currentDate;
    USER_NEW_APPT.waitDays = Math.ceil(timeDifference / (1000 * 60 * 60 * 24));
  }

  var closestHospitals = JSON.parse(localStorage.getItem('listOfClosestHospitals'))

  const closestHospitalsObjects = []
  if (closestHospitals){
    for (var i = 1; i < closestHospitals.length; i++){
      closestHospitalsObjects.push(
        <div id="newlyFoundApptContainer">
          <div id="newlyFoundApptHeader">NEWLY FOUND APPT#{i+1}</div>
            <div id="newlyFoundApptDescription">
            <div id="newlyFoundDetails">
              <div><b>Date:</b> {new Date(new Date().setDate(new Date().getDate() + closestHospitals[i].medianWait)).toISOString().split('T')[0]}</div>
              <div 
                style={hasPaid ? {} : {filter: 'blur(10px)'}}
              >
                <b>Hospital:</b> {closestHospitals[i].Site}
              </div>
              <div><b>Earlier by this many days:</b> {((new Date(USER_CURRENT_APPT.appointmentDate) - new Date(new Date().setDate(new Date().getDate() + closestHospitals[i].medianWait))) / (1000 * 60 * 60 * 24)).toFixed(0)}
              </div>
              <div><b>Wait days:</b> {closestHospitals[i].medianWait}</div>
              <div 
                style={hasPaid ? {} : {filter: 'blur(10px)'}}>
                <b>Distance:</b> {closestHospitals[i].distance.toFixed(1)} KM</div>
              </div>
              {/* <Button className="receive-form-button" onClick={showInfoMessage}>Receive Form</Button> */}

            </div>
          </div>
      )
    }
  }
  return (
    <div className="App">
      <Header />
      <Menu isLoged={isLoged} weight='book' />
      <div id="BookPageBackgroundContainer">
        <div id="bookingNotice">Wait Times Found</div>
        <div id="BookPageForms">
          <div id="userCurrentApptContainer">
            <div id="userCurrentApptHeader">USER CURRENT APPT</div>
            <div id="userCurrentApptDescription">
              <div id ="userDetails">
                <div><b>TYPE:</b> {USER_CURRENT_APPT ? USER_CURRENT_APPT.appointmentType.toUpperCase() : '' }</div>
                <div><b>DATE:</b> {USER_CURRENT_APPT ? USER_CURRENT_APPT.appointmentDate : '' }</div>
                <div><b>LOCATION:</b> {USER_CURRENT_APPT ? USER_CURRENT_APPT.livingPlace : '' }</div>
              </div>
            </div>
          </div>
          <div id="newlyFoundApptContainer">
            <div id="newlyFoundApptHeader">NEWLY FOUND APPT{closestHospitals.length > 1?"#1":""}</div>
            <div id="newlyFoundApptDescription">
              <div id="newlyFoundDetails">
                <div><b>Date:</b> {USER_NEW_APPT ? USER_NEW_APPT.appointmentDate : ""}</div>
                  <div 
                    style={hasPaid ? {} : {filter: 'blur(10px)'}}
                  >
                    <b>Hospital:</b> {USER_NEW_APPT ? USER_NEW_APPT.appointmentPlace : ""}
                  </div>
                <div><b>Earlier by this many days:</b> {USER_NEW_APPT ? USER_NEW_APPT.appointmentDateSoonerCount : ""}</div>
                <div><b>Wait days:</b> {USER_NEW_APPT ? USER_NEW_APPT.waitDays : ""}</div>
                <div 
                    style={hasPaid ? {} : {filter: 'blur(10px)'}}
                  >
                <b>Distance:</b>{closestHospitals[0].distance.toFixed(1)} KM
              </div>
              </div>
            </div>
          </div>

          {closestHospitals ? (
            <div>{closestHospitalsObjects}</div>
          ) : (<div></div>) }

            <div>
                {showPaymentForm ? (
                    <PaymentForm /> /* Render the PaymentForm when showPaymentForm is true */
                ) : (
                    <div id='BookPagePurchaseButton'>
                        <Button className="buttonBackgroundModule"
                            onClick={handlePayClick}
                            name="Pay"
                        >Pay</Button>
                    </div>
                )}
            </div>
            </div>
      </div>
      <div id='BookPageDisclaimer'>
        Disclaimer: This website is independently operated and has no affiliation with any hospitals or imaging departments. It is designed to assist users in identifying potential locations that may have shorter wait times for medical imaging services.

Please note that all appointment times displayed are estimates and are based on the median wait times for CT and MRI at each location, as per data from Ontario Health. These times are subject to change and should be used as a guideline only.

This website is not responsible for submitting or managing requests for appointments. It does not facilitate the completion or submission of any forms required by healthcare facilities. Users seeking to book an imaging appointment at any specified center must do so through their doctor's office. We strongly recommend verifying the current wait times and appointment details with the respective facility.
      </div>
    </div>
  );
}

function App() {

}
export default BookPage;