import React, { useEffect, useState } from 'react';
import Button from "../Button";
import { useNavigate } from 'react-router-dom';
import jwt_decode from 'jwt-decode';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Menu from "../Menu";
import Header from "../Header";
import '../css/LoginPageStyles.css';
import isLoged from "../functions/IsLoged";
const { LINK } = require('../../config.js');


function LoginPage() {
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const checkLoggedIn = async () => {
            const logged = await isLoged();
            setIsLoggedIn(logged);
        };

        checkLoggedIn();
    }, []);

    async function loginRequest() {
        var data = {
            email: document.getElementById('email').value,
            password: document.getElementById('password').value,
        };
        console.log('base');
        console.log(`data:`);
        console.log(data);
        try {
            console.log(`Sent request ${data.email}`);
            const proxyUrl = LINK || 'http://16.16.201.50:81';
            const res = await fetch('/api/users/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            });
            console.log(res.status);
            const answer = await res.json();
            if (res.status === 200) {
                toast.success('Logged in!', {
                    position: "top-center",
                    autoClose: false,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                let accessToken = answer.accessToken;
                localStorage.setItem('accessToken', accessToken);
                localStorage.setItem('userEmail', data.email);
                localStorage.setItem('userPassword', data.password);
                navigate('/');
            } else {
                toast.warn(answer.message, {
                    position: "top-center",
                    autoClose: false,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            }
        } catch (error) {
            console.error('There was an error:', error);
        }
    }

    if (!isLoggedIn) {
        return <LoginPageNotAccessed loginRequest={loginRequest} />;
    } else {
        return <LoginPageAccessed />;
    }
}

function LoginPageNotAccessed({ loginRequest }) {
    return (
        <div>
            <Header />
            <Menu weight='login' />
            <div id="login-page-not-logged-main-container">
                <div id="first-string-login-page-not-logged">Welcome back!</div>
                <div id="second-string-login-page-not-logged">Please log in to your account.</div>
                <input className="form-control input-login" type="email" placeholder="Email" id="email" />
                <input className="form-control input-login password-login-input" type="password" placeholder="Password" id="password" />
                <Button id="login-page-login-button" onClick={loginRequest} name="Login" />
                <div id="bottom-text-login-page-not-logged">Or <a href='/signup'><i className="fas fa-link">Sign up</i></a></div>
            </div>
        </div>
    )
}

function LoginPageAccessed() {
    return (
        <div>
            <Menu weight='login' />
            <h1>YOU ARE LOGGED IN NOW!</h1>
        </div>
    )
}

export default LoginPage;
