import React, { useState } from "react";
import "./css/IntakeForm.css";
import IntakeFormInput from "./IntakeFormInput";
import MainPageResponceBlock from "../components/MainPageResponceBlock";
import Select from "react-select";
import "./css/IntakeFormInput.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
const { LINK } = require("../config.js");

function startLoader() {
  document.getElementById("IntakeFormButton").disabled = true;
  // document.getElementById('loaderBlock').style.display = "block";
}

function stopLoader() {
  document.getElementById("IntakeFormButton").disabled = false;
  document.getElementById("loaderBlock").style.display = "none";
}

function IntakeForm(props) {
  //do something else
  // toast.update(id, { render: "All is good", type: "success", isLoading: false });

  // Use state to manage form values
  const [formData, setFormData] = useState({
    livingPlace: "",
    appointmentType: "",
    appointmentDate: "",
    maxDistance: "30", // Default value for maxDistance
  });

  React.useEffect(() => {
    // Check if there is data in localStorage
    const storedData = JSON.parse(localStorage.getItem("USER_CURRENT_APPT"));

    // If there is data, set the form values
    if (storedData) {
      console.table(storedData);
      setFormData(storedData);
    }
  }, []);

  const setFormVisibility = props.setFormVisibility;
  const setResponseData = props.setResponseData;
  function formatLivingPlace(e) {
    let value = e.target.value.toUpperCase();
    value = value.replace(/[^A-Z0-9]/g, "").slice(0, 6);
    e.target.value = value;

    // Check if the input matches the "A1A2B2" format
    const regex = /^[A-Z]\d[A-Z]\d[A-Z]\d$/;
    if (!regex.test(value) && value.length === 6) {
      toast.error('Invalid format. Please use the "A1A2B2" format.', {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    }
  }
  function submitIntakeFormFunction() {
    const livingPlace = document.getElementById("livingPlace").value;
    const regex = /^[A-Z]\d[A-Z]\d[A-Z]\d$/;

    if (!regex.test(livingPlace) && livingPlace.length === 6) {
      toast.error(
        'Invalid format for living place. Please use the "A1A2B2" format.',
        {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        }
      );
      return;
    }
    const proxyUrl = LINK || "http://16.16.201.50:81";
    console.log(`LINK: ${proxyUrl}`);
    startLoader();
    localStorage.setItem(
      "USER_CURRENT_APPT",
      JSON.stringify({
        livingPlace: document.getElementById("livingPlace").value,
        appointmentType: document.getElementById("appointmentType").value,
        appointmentDate: document.getElementById("appointmentDate").value,
        maxDistance: document.getElementById("maxDistance").value,
      })
    );
    if (
      !document.getElementById("livingPlace").value ||
      !document.getElementById("appointmentType").value ||
      !document.getElementById("appointmentDate").value ||
      !document.getElementById("maxDistance").value
    ) {
      toast.warn("All fields are mandatory", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      return;
    }
    const id = toast.loading("Please wait...");
    const response = fetch("/api/hospitals/getclosest", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        livingPlace: document.getElementById("livingPlace").value,
        appointmentType: document.getElementById("appointmentType").value,
        appointmentDate: document.getElementById("appointmentDate").value,
        maxDistance: document.getElementById("maxDistance").value,
      }),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        stopLoader();
        console.log(`data:`, data);
        toast.dismiss(id);
        if (data["message"]) {
          toast(data["message"], {
            position: "top-center",
            autoClose: false,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
          });
          // toast.update(id, { render: , type: toast.TYPE.WARNING, isLoading: false });
          return;
        }
        setResponseData(data);
        localStorage.setItem(
          "listOfClosestHospitals",
          JSON.stringify(data.listOfClosestHospitals)
        );
        setFormVisibility(false);
      });
  }

  return (
    <div id="IntakeFormMainContainer">
      <div id="IntakeFormSmallContainer">
        <div
          id="loaderBlock"
          class="spinner-border text-dark"
          style={{
            marginTop: "30vh",
            position: "absolute",
            left: "50%",
            display: "none",
          }}
          role="status"
        >
          <span class="sr-only">Loading...</span>
        </div>
        <div id="IntakeFormHeader">Intake Form</div>
        <div id="IntakeFormDescription">
          My postal code is
          <IntakeFormInput
            mobile="true"
            name="livingPlace"
            placeholder="M5V3K1"
            width="6ch"
            onBlur={(e) => {
              e.target.value = e.target.value.toUpperCase();
              formatLivingPlace(e);
            }}
            default={formData.livingPlace} // Set the value from state
          />
          and have a
          <select
            id="appointmentType"
            className="IntakeFormInput IntakeFormSelect"
            style={{ width: "5vw", minWidth: "max-content" }}
            required
            value={formData.appointmentType} // Set the value from state
            onChange={(e) =>
              setFormData({ ...formData, appointmentType: e.target.value })
            }
          >
            <option value="" disabled selected hidden>
              CT/MRI
            </option>

            <option value={"ct"}>CT</option>
            <option value={"mr"}>MRI</option>
          </select>
          appointment booked on
          <IntakeFormInput
            mobile="true"
            type="date"
            name="appointmentDate"
            required
            default={formData.appointmentDate} // Set the value from state
            onChange={(e) =>
              setFormData({ ...formData, appointmentDate: e.target.value })
            }
          />
          .
          <br />I am willing to travel within{" "}
          <IntakeFormInput
            onlyNums="true"
            min="0"
            name="maxDistance"
            style={{ textDecoration: "none" }}
            default={formData.maxDistance}
          />{" "}
          KM’s to get it done sooner.
        </div>
        <button id="IntakeFormButton" onClick={submitIntakeFormFunction}>
          Submit
        </button>
      </div>
    </div>
  );
}

export default IntakeForm;
