import React from 'react';
import './css/MenuStyles.css';
import lins from './img/lins.png';
import logo from './img/logo.jpg';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import BurgerMenuForMobileVersion from './BurgerMenuForMobileVersion.js';

function Menu(props) {
    var body = document.body;
    const setBodyOverflow = (overflow) => {
        window.scroll(0, 0)
        document.body.style.overflow = overflow;
    }
    
    return (
        <div id='containerMenu'>
        <ToastContainer
        position="top-center"
        autoClose={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        theme="light"
        />
        <ul className='customList'>
            <li id='menu-logo-photo'><a href='/' className='linkMenuPhoto'>
                <img src={logo}/>
                </a></li>
            <li id='bookBlock' ><a href='/book' className={props.weight == 'book' ? 'weight' : 'linkMenu'}>BOOK</a></li>
            <li><a href='/faq' className={props.weight == 'faq' ? 'weight' : 'linkMenu'}>FAQ</a></li>
            <li><a href='/profile' className={props.weight == 'profile' ? 'weight' : 'linkMenu'}>PROFILE</a></li>
            {/* <li><div id='searchAndLins'><a href='/search' className={props.weight == 'search' ? 'weight' : 'linkMenu'}>SEARCH
            <div id='container-for-lins-photo'>
                <img src={lins} alt='' id='linsPhoto' />
            </div>
            </a></div></li> */}
            {props.isLoged ?
            <li style={{ marginRight: '5vh' }}><a href='/' onClick={() => {
                localStorage.setItem('accessToken', '')
                localStorage.setItem('googleUser', '')
            }} className={props.weight == 'login' ? 'weight' : 'linkMenu'}>LOG OUT</a></li>
            :
            <li id='menu-block-login-or-signup-link'><a href='/login' className={props.weight == 'login' ? 'weight' : 'linkMenu'}>LOG IN</a></li>}
        </ul>
        <div id='burger-menu-mobile-version-container'>
            <BurgerMenuForMobileVersion isLoged={props.isLoged} setBodyOverflow={setBodyOverflow}></BurgerMenuForMobileVersion>
        </div>
    </div>
    )
}

export default Menu;