import React, { useEffect, useState } from 'react';
import '../components/css/IntakeFormInput.css';

function IntakeFormInput(props){
    const inputStyle = {}
    // if (props.mobile){
    // }else{
    //     inputStyle.width = props.width || "14vw"
    // }

    const [inputValue, setValue] = useState('');

    const formatDate = (value) => {
        // Remove all non-digits characters
        const numbers = value.replace(/[^\d]/g, '');

        // Split the string into year, month, day parts
        const year = numbers.slice(0, 4);
        const month = numbers.slice(4, 6);
        const day = numbers.slice(6, 8);

        // Combine them with dashes, but only if the parts exist
        return [year, month, day].filter(Boolean).join('-');
    };
    
    useEffect(() => {
        if (props.default){
            setValue(props.default);
        }
    }, [props.default])

    const checkInputForNumbers = (event) => {
        // Use event.key for better cross-browser compatibility
        if (props.onlyNums && (event.key < '0' || event.key > '9')) {
            // If it's not a number, prevent the input
            event.preventDefault();
            return;
        }

        const newValue = event.target.value;
        // If the input should be formatted as a date
        if (props.type === 'date') {
            const formattedValue = formatDate(newValue);
            setValue(formattedValue);
        } else {
            setValue(newValue);
        }
    };
    return (
        <input 
        type={props.type || 'text'} 
        value={inputValue}
        name={props.name} 
        id={props.name}
        min={props.min}
        style={inputStyle}
        className='IntakeFormInput'
        onChange={checkInputForNumbers}
        onBlur={props.onBlur}
        placeholder={props.placeholder || ''}
        />
    )
}

export default IntakeFormInput;